/* CHECKBOXES */
.checkbox-group {
  display: inline-flex;
  padding-left: 0;

  & + .form-check-label {
    margin-left: rem(10);
  }

  label, .form-check-label {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 0;
  }

  /* Base for label styling */
  /*Joint styles*/
  [type="checkbox"]:not(:checked),
  [type="checkbox"]:checked,
  [type="radio"]:checked,
  [type="radio"]:not(:checked) {
    position: absolute;
    left: -9999px;
  }

  [type="checkbox"]:not(:checked) + label,
  [type="checkbox"]:checked + label,
  [type="radio"]:checked + label,
  [type="radio"]:not(:checked) + label {
    flex-basis: rem(35);
    width: rem(35);
    height: rem(35);
    flex-shrink: 0;
    position: relative;
    //overflow: hidden;
  }

  [type="checkbox"]:not(:checked) + label:before,
  [type="checkbox"]:checked + label:before,
  [type="radio"]:checked + label:before,
  [type="radio"]:not(:checked) + label:before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    flex-shrink: 0;
    width: 100%;
    height: 100%;
    border: solid 1px #CEDAE9;
  }

  /* --------- CHECKBOX STYLES ------------ */
  [type="checkbox"]:not(:checked) + label:before,
  [type="checkbox"]:checked + label:before,
  [type="checkbox"]:not(:checked) + label:after,
  [type="checkbox"]:checked + label:after {
    border-radius: rem(5);
  }

  [type="checkbox"]:not(:checked) + label:before,
  [type="checkbox"]:checked + label:before {
    background-color: #ffffff;
    overflow: hidden;
  }

  [type="checkbox"]:checked + label:before {
    background-color: #A7C9D9;
  }

  /* checked mark aspect */
  [type="checkbox"]:not(:checked) + label:after,
  [type="checkbox"]:checked + label:after {
    content: '\e94a';
    font-family: '-iconfont';
    color: #000;
    font-weight: bold;
    margin-left: rem(1);
    background-size: contain;
  }

  [type="checkbox"]:not(:checked) + label:after,
  [type="checkbox"]:checked + label:after {
    color: #000;
    position: absolute;
    left: rem(2);
    top: rem(2);
    transition: all .2s;
  }

  [type="checkbox"]:not(:checked) + label:after,
  [type="checkbox"]:checked + label:after {
    font-size: rem(13);
  }

  /* checked mark aspect changes */
  [type="checkbox"]:not(:checked) + label:after {
    opacity: 0;
    transform: scale(0);
  }

  [type="checkbox"]:checked + label:after {
    opacity: 1;
    transform: scale(1);
  }

  /* disabled checkbox */
  [type="checkbox"]:disabled + label {
    color: #A5A5A5;
  }

  [type="checkbox"]:disabled:not(:checked) + label:before,
  [type="checkbox"]:disabled:checked + label:before {
    box-shadow: none;
    border-color: #CEDAE9;
    background-color: #CEDAE9;
  }

  [type="checkbox"]:disabled:checked + label:after {
    color: #999;
  }

  /* --------- RADIO CHECK STYLES --------- */
  [type="radio"]:checked + label,
  [type="radio"]:not(:checked) + label {
    cursor: pointer;
    line-height: rem(20);
    display: inline-block;
    color: #666;
  }

  [type="radio"]:checked + label:before,
  [type="radio"]:not(:checked) + label:before {
    border-radius: 50%;
    background: #CEDAE9;
  }

  [type="radio"]:checked + label:after,
  [type="radio"]:not(:checked) + label:after {
    content: '';
    flex-shrink: 0;
    flex-basis: rem(35);
    width: rem(35);
    height: rem(35);
    background: #55A08F;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 50%;
    transition: all 0.2s ease;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  }

  [type="radio"]:not(:checked) + label:after {
    opacity: 0;
    transform: scale(0);
  }

  [type="radio"]:checked + label:after {
    opacity: 1;
    transform: scale(1);
  }
}