html {
  font-size: $baseFontSize + px;
  overflow-x: hidden;

  @media (max-width: 1699px) {
    font-size: calc(11px + .4vw);
  }

  @media (max-width: 1499px) {
    //font-size: calc(9px + .4vw);
  }

  @media (max-width: 1299px) {
    font-size: calc(11px + .4vw);
  }
}

.anchor-block {
  border: 1px solid blue;
}

body {
  font-size: 1rem;
  width: 100%;
  overflow-x: hidden;
}

main {
  display: block;
  min-height: 100vh;
}

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
  //font-family: 'Montserrat Alternates', sans-serif;
  line-height: 1;
  color: inherit;
}

h1, .h1 {
  font-size: 4.5714rem;
  font-weight: 600;
  margin-bottom: 40px;
}

h2, .h2 {
  font-size: 3.4285rem;
  font-weight: 600;
}

h3 {
  font-size: calc(35px + (40 - 35) * ((100vw - 1200px) / (1920 - 1200)));
  margin-bottom: calc(44px + (47 - 44) * ((100vw - 1200px) / (1920 - 1200)));
}

/*

h4, .h4,
.text-xl {
  font-size: 1.714rem;
}

h5, .h5 {
  font-size: 1.1428rem;
  font-weight: 700;
  letter-spacing: 2.5px;
}

h6, .h6 {
  font-size: 1rem;
}

.text-xl {
  line-height: 1.5;
}
*/

p {
  margin-top: 0;
  margin-bottom: 1rem;

  &:last-child {
    margin-bottom: 0;
  }
}

a {
  background-color: transparent;
  -webkit-text-decoration-skip: objects;

  &:not([href]):not([tabindex]),
  &:not([href]):not([tabindex]):hover,
  &:not([href]):not([tabindex]):focus {
    color: inherit;
    text-decoration: none;
  }

  &:hover {
    color: inherit;
    text-decoration: underline;
  }
}

ul, ol {
  margin: 0;
}

svg:not(:root) {
  overflow: hidden;
}

label {
  display: inline-block;
  margin-bottom: .5rem;
}

button {
  border-radius: 5px;

  &:focus {
    outline: none;
  }
}

.circle {
  border-radius: 50%;
}

/*IMGS*/
.w-img {
  overflow: hidden;

  img {
    width: 100%;
    height: auto;
  }

  .img-cover {
    object-fit: cover;
    min-width: 100%;
    min-height: 100%;
  }
}

.flex-column {
  display: flex;
  flex-direction: column;
}

/*
LAYOUTS*/
@import "layouts";
@import "checkbox";

/*BUTTONS*/
.btn {
  max-width: 100%;
  min-width: rem(244);
  font-weight: 700;
  line-height: 1.55;
  padding: rem(20);
  border: 1px solid transparent;
  border-radius: rem(4);
  box-shadow: 0 rem(4) rem(4) rgba(0, 0, 0, 0.25);
  transition: 0.2s ease-in-out;
}

.btn:hover, .btn:focus {
  text-decoration: none;
}

.btn:focus, .btn.focus {
  outline: 0;
  box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
}

.btn-success {
  color: #fff;
  border-width: 1px;
  background-color: $success-color;
  border-color: $success-color;
  //border-style: solid;

  &:hover, &:focus {
    background-color: inherit;
    color: darken($success-color, 5%);
    border-color: darken($success-color, 5%);
  }
}

.nav-link {
  padding: 0;
}

/*
--------------------- PAGES ---------------------*/
@import "layouts";

a {
  transition: color 0.3s ease-in-out, opacity 0.3s ease-in-out;
  color: inherit;
  &:hover {
    text-decoration: none;
  }
}

.section {
  padding-top: 3.6428rem; //pt 51px;
}

.grid-row {
  margin-bottom: 100px;

  &:last-child {
    margin-bottom: 0;
  }

  .grid-item {
    width: 100%;
    margin-bottom: 16px;
  }

  .slider-col {
    order: 1;
  }

  @media (min-width: 768px) {
    flex-wrap: wrap;
    margin-bottom: 16.7857rem;

    .grid-item {
      flex: 0 0 70%;
      max-width: 70%;

      &.txt-info-col {
        flex: 0 0 30%;
        max-width: 30%;
        order: initial;
      }

      &.slider-col {
        order: initial;
      }
    }
  }

  @media (min-width: 576px) {
    .grid-item {
      flex: 0 0 60%;
      max-width: 60%;

      &.txt-info-col {
        flex: 0 0 40%;
        max-width: 40%;
      }
    }
  }
}

.page-wrapper {
  min-height: 100vh;
  display: flex;
  //flex-wrap: wrap;
  justify-content: flex-end;
}

.page-sections {
  position: relative;
  width: calc(100% - #{$sidebar-width});
  /* div:nth-child(odd) {
     background: lightgray;
   }*/
}

.full-height-section {
  min-height: 100vh;
}

.section-item {
  position: relative;
  //display: flex;
  //justify-content: center;
  //align-items: center;
  padding: calc(35px + (50 - 35) * ((100vw - 1200px) / (1920 - 1200))) 0;

  //margin-top: -1px;

  &.light-content {
    color: #fff;
  }
}

p, .desc {
  font-size: calc(14px + (20 - 14) * ((100vw - 1200px) / (1920 - 1200)));
  line-height: calc(21px + (24 - 21) * ((100vw - 1200px) / (1920 - 1200)));

  & + {
    & .w-select, & .btn {
      margin-top: rem(40);
    }
  }
}

.pre-heading {
  font-family: 'Vollkorn', serif;
  font-size: calc(27px + (40 - 27) * ((100vw - 1200px) / (1920 - 1200)));
  line-height: calc(35px + (56 - 35) * ((100vw - 1200px) / (1920 - 1200)));
  margin-bottom: calc(5px + (3 - 5) * ((100vw - 1200px) / (1920 - 1200)));
}

.section-title {
  line-height: 1;
  text-transform: uppercase;
  font-size: calc(45px + (65 - 45) * ((100vw - 1200px) / (1920 - 1200)));
  margin-bottom: calc(25px + (40 - 25) * ((100vw - 1200px) / (1920 - 1200)));
}

/*SIDEBAR*/
@import "stiky-nav";

.w-select {
  width: auto !important;
}

.custom-select {
  min-width: rem(367);

  .select2-container {
    min-width: rem(367);
  }
  .select2-container .select2-selection--single {
    height: rem(63);
    background-color: #C45D62;
    border-radius: rem(4);
    border-color: #C45D62;
  }

  .select2-container--default .select2-selection--single .select2-selection__rendered {
    line-height: rem(63);
    padding-right: rem(35);
    padding-left: rem(23);
  }

  .select2-container--default .select2-selection--single .select2-selection__rendered,
  .select2-container--default .select2-selection--single .select2-selection__placeholder {
    color: #fff;
    font-weight: bold;
  }

  .select2-container--default .select2-selection--single .select2-selection__arrow {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: rem(17);
    top: 50%;
    right: rem(20);
    transform: translateY(-50%);
  }

  .select2-container--default .select2-selection--single .select2-selection__arrow b {
    position: relative;
    top: 1px;
    margin: 0;
    border-color: #fff transparent transparent transparent;
    border-style: solid;
    border-width: rem(16) rem(8) 0 rem(8);
  }
}

.reimagined-sec-1 {
  display: flex;
  align-items: center;
  //position: relative;
  padding: rem(64) 0;
  background: no-repeat url('../img/png/sec1-bg.png');
  background-size: contain;
  background-position: left 130%;

  @media(max-height: 1200px) {
    background-size: 100%;
  }

  @media(max-width: 1699px) {
    background-size: cover;
  }

  &:before {
    content: '';
    display: block;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 0;
    width: 80%;
    max-width: rem(793);
    height: 100%;
    background: no-repeat url('../img/png/sec1-img.png');
    background-size: 100%;
    background-position: 0 rem(64);
    z-index: 0;

    @media(max-width: 1699px) {
      background-position: right center;
    }

    @media(max-height: 1000px) {
      background-position: right center;
      background-size: contain;
    }
  }

  .text-column {
    //padding: calc(20px + (50 - 20) * ((100vw - 1300) / (1600 - 320)));
    padding-left: rem(80);
    z-index: 3;

    .w-select {
      width: rem(367);
    }
  }

  .column-content__wrap {
    max-width: rem(786);
    //max-width: 70%;
  }

  @media(min-width: 1699px) {
    .text-column {
      padding-left: 182px;
    }
  }
}

.reimagined-sec-2 {
  border: 1px solid red;
  //position: relative;
  background-color: #DBB072;
  padding-top: rem(350);
  padding-bottom: rem(170);

  &:before, &:after {
    content: '';
    position: absolute;
  }

  &:before {
    position: absolute;
    top: rem(-3);
    left: 0;
    right: 0;
    height: 100%;
    min-height: rem(850);
    background: no-repeat url('../img/png/sec2-top-bg.png');
    background-size: 100%;
    z-index: 0;

    @media(max-width: 1699px) {
      background-size: 100%;
    }
  }

  .column {
    z-index: 3;
  }

  .section-img__group {
    position: relative;
    width: rem(561);
    height: rem(561);

    &:before {
      content: '';
      position: absolute;
      top: rem(22);
      right: rem(22);
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background-color: #CEDAE9;
    }

    @media(max-width: 1299px) {
      max-width: 100%;
      height: auto;
    }
  }

  .section-img {
    position: relative;
    width: 100%;
    height: 100%;
  }

  .text-column {
    .custom-select .select2-container {
      min-width: rem(523);
      width: auto !important;
    }
  }

  [class*="column"] {
    @media(max-width: 1299px) {
      max-width: 50%;
    }
  }
}

/*W ORKSCAPES */
.workspaces-holder {
  background-image: url("../img/png/workspaces-top-bg.png");
  background-size: contain;
  background-position: right rem(-60);
  background-repeat: no-repeat;
  overflow: hidden;
}

.workspaces-sec-1 {
  padding: rem(100) 0;
  display: flex;
  align-items: center;

  .text-column {

    @media(min-width: 1300px) {
      padding-left: calc(40px + (182 - 40) * ((100vw - 1200px) / (1920 - 1200))) !important;
    }
  }
}

.img-circle-txt__group {
  position: relative;

  &:before {
    content: '';
    position: absolute;
    left: -35px;
    bottom: 0;
    width: 100%;
    height: 95%;
    background: no-repeat url('../img/png/workspaces-puzzles.png');
    background-position: 0 center;
    background-size: contain;
    z-index: 0;
  }

  .circle-text {
    position: relative;
    display: inline-flex;
    align-items: center;
    width: calc(350px + (508 - 350) * ((100vw - 1200px) / (1920 - 1200)));
    height: calc(350px + (508 - 350) * ((100vw - 1200px) / (1920 - 1200)));
    color: #fff;
    font-size: 20px;
    line-height: 24px;
    border-radius: 50%;
    padding: calc(43px + (57 - 43) * ((100vw - 1200px) / (1920 - 1200)));
    background-color: #658CAA;
    box-shadow: 0 rem(4) rem(10) rgba(0, 0, 0, 0.25);

    @media (max-width: 1499px) {
      font-size: calc(14px + (20 - 14) * ((100vw - 1200px) / (1920 - 1200)));
    }

    .title {
      text-align: center;
      font-size: rem(40);
      margin-bottom: rem(20);
    }

    &._white {
      color: inherit;
      background-color: #fff !important;
    }
  }

  .section-img {
    position: relative;
    max-width: calc(350px + (455 - 350) * ((100vw - 1200px) / (1920 - 1200)));
    margin-top: rem(-100);
    margin-left: rem(122);

    .w-img, &:before {
      border-radius: rem(4)
    }

    &:before {
      content: '';
      position: absolute;
      top: rem(18);
      right: rem(-18);
      width: 100%;
      height: 100%;
      background-color: #CEDAE9;
    }

    .w-img {
      position: relative;
    }
  }
}

.furniture-sec-1, .furniture-sec-2 {
  background-color: #EDC489;
}

.furniture-sec-1 {
  background-image: url("../img/png/furniture-top-bg-light.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: top center;
}

.submission-holder {
  position: relative;

  .bg-layer {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 0;

    & > *, &:before, &:after {
      position: absolute;
    }

    .puzzle-img {
      top: 50%;
      right: rem(106);
      transform: translateY(-50%);
      max-width: calc(170px + (306 - 170) * ((100vw - 1200px) / (1920 - 1200)));
      //max-height: 80%;
      z-index: 2;

      img {

      }

      @media(max-width: 1024px) {

      }
    }

    &:before, &:after {
      content: '';
    }

    &:before {
      top: 0;
      left: 0;
      right: 0;
      height: 100%;
      background: no-repeat url("../img/png/last-section-top-bg.png");
      background-position: center rem(-190);
      z-index: 1;
    }

    &:after {
      right: calc(100px + (270 - 100) * ((100vw - 1200px) / (1920 - 1200)));
      top: calc(200px + (55 - 200) * ((100vw - 1200px) / (1920 - 1200)));
      width: calc(320px + (500 - 320) * ((100vw - 1200px) / (1920 - 1200)));
      height: calc(320px + (500 - 320) * ((100vw - 1200px) / (1920 - 1200)));
      border-radius: 50%;
      background: no-repeat url("../img/png/submission-circle-img.png");
      background-size: contain;
      overflow: hidden;
      z-index: 0;
    }
  }

  .content-holder {
    z-index: 2;
  }

  .text-column {
    max-width: 60%;

    .column-content__wrap {
      width: 100%;
      max-width: 516px;
    }
  }
}

.card {
  border-radius: rem(4);
}

.card-primary {
  .card-header, .card-footer {
    color: #fff;
    background-color: #658CAA;
  }
}

.card-body {
  min-height: rem(200);
}

.card-header {
  .card-title {
    margin-bottom: 0;
  }
}

.card-footer {
  padding-top: calc(23px + (27 - 23) * ((100vw - 1200px) / (1920 - 1200)));
  padding-bottom: calc(23px + (27 - 23) * ((100vw - 1200px) / (1920 - 1200)))
}

.card-header, .card-footer {
  padding: calc(21px + (24 - 21) * ((100vw - 1200px) / (1920 - 1200))) calc(15px + (21 - 15) * ((100vw - 1200px) / (1920 - 1200)));
}

.workspaces-feedback__holder {
  .grid {
    @include custom-grid-padding(rem(0));
  }

  .carousel-preview__main-img {
    margin-bottom: rem(22);
  }

  .carousel-preview__thumbnails {
    display: flex;
    flex-wrap: wrap;
    margin: -2px;

    .thumbnail {

      width: calc(100% / 3);
      color: #fff;
      padding: 2px;

      &.active {
        .title {
          background-color: #C45D62;
        }
      }
    }

    .thumbnail-inner {
      height: 100%;
      display: flex;
      flex-direction: column;
    }

    .title {
      flex-grow: 1;
      min-height: rem(39);
      text-align: center;
      font-size: calc(14px + (20 - 14) * ((100vw - 1200px) / (1920 - 1200)));
      line-height: calc(21px + (24 - 21) * ((100vw - 1200px) / (1920 - 1200)));
      padding: rem(7) rem(6);
      background-color: #658CAA;
    }

    .media-holder {
      height: rem(67);
    }
  }
}

.workspaces-feedback__step {
  & + .workspaces-feedback__step {
    margin-top: rem(132);
  }
}

.preview-column {
  max-width: rem(647);
}

.preview-column__option-holder {
  & + .preview-column__option-holder {
    margin-top: rem(18);
  }
  & > * {
    height: rem(129);
  }

  .color-box {
    width: rem(210);
    flex-basis: rem(210);
    max-width: rem(210);
    flex-shrink: 0;
  }

  .text-box {
    display: flex;
    align-items: center;
    font-weight: bold;
    font-size: calc(35px + (40 - 35) * ((100vw - 1200px) / (1920 - 1200)));
    line-height: 1.2;
    color: #fff;
    padding: rem(19) rem(15);
    border-radius: rem(6);
    margin-left: rem(-3);
    background-color: #658CAA;
  }
}

.fb-options {
