.container-fluid, .container {
  padding-right: calc(40px + (88 - 40) * ((100vw - 1200px) / (1920 - 1200)));
  padding-left: calc(40px + (88 - 40) * ((100vw - 1200px) / (1920 - 1200)));

  & > .row {
    margin: 0 calc(40px + (-88 - 40) * ((100vw - 1200px) / (1920 - 1200)));

    & > [class*="column"] {
      padding-right: calc(40px + (88 - 40) * ((100vw - 1200px) / (1920 - 1200)));
      padding-left: calc(40px + (88 - 40) * ((100vw - 1200px) / (1920 - 1200)));
    }
  }

  @media (max-width: 1299px) {
    & > .row {
      margin: - -40px;
    }
  }
}

.container {
  margin: 0 auto;
}

.row {
  display: flex;
  flex-wrap: wrap;

  &.nowrap {
    flex-wrap: nowrap;
  }
}

.text-left-grid {
  .text-column {
    @media (min-width: 1299px) {
      padding-left: rem(182) !important;
    }
  }
}

.d-flex {
  display: flex;
}

.d-inline-flex {
  display: inline-flex;
}

.flex-y-center {
  display: flex;
  align-items: center;
}

.flex-x-center {
  display: flex;
  justify-content: center;
}

.column {
  flex-basis: 0;
  flex-grow: 1;
  max-width: 100%;
}

.column-auto {
  flex: 0 0 auto;
  width: auto;
  max-width: none;
}

.column-33 {
  width: calc(100% / 3);
  flex-basis: calc(100% / 3);
  max-width: calc(100% / 3);
}

.column-50 {
  width: 50%;
  flex-basis: 50%;
  max-width: 50%;
}

.text-center {
  text-align: center;
}
