.main-nav {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: $sidebar-width;
  color: #084C79;
  margin: 0;
  padding: rem(79) rem(45) rem(79) rem(45);
  box-shadow: rem(6) rem(4) rem(20) rgba(0, 0, 0, 0.25);
  background: #FFFFFF;
  z-index: 100;
  transition: opacity .5s;

  .logo {
    width: rem(196);
    min-height: rem(111);
    margin-bottom: rem(140);

    img {
      max-width: 100%;
    }
  }
}

.main-nav__list {
  list-style: none;
}

.main-nav__list-item {
  &:first-child {
    .main-nav__link {
      padding-top: 0;
    }
  }

  &:last-child {
    .main-nav__link {
      &:after {
        content: none;
      }
    }
  }
}

.main-nav__link {
  position: relative;
  display: inline-block;
  font-size: rem(20);
  padding-left: rem(36);
  padding-bottom: rem(71);

  & > span:not(.main-nav-menu-dot) {
    border-bottom: rem(4) solid transparent;
  }

  &:after {
    content: '';
    position: absolute;
    width: 1px;
    height: 100%;
    left: rem(13.5);
    top: rem(13.5);
    background-color: #B6B6B6;
    z-index: -1;
  }

  &.active {
    font-weight: bold;

    & > span:not(.main-nav-menu-dot) {
      border-color: #C45D62;
    }

    .main-nav-menu-dot {
      &:before {
        border-width: rem(6);
      }
    }
  }
}

.main-nav-menu-dot {
  position: absolute;
  left: 0;
  top: 2px;
  width: rem(27);
  height: rem(27);
  border-radius: 50%;
  background-color: #fff;
  z-index: 0;

  &:before {
    content: '';
    position: absolute;
    width: rem(21);
    height: rem(21);
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    border-radius: 50%;
    border: 1px solid #658CAA;
  }
}